import { Injectable } from '@angular/core';

@Injectable()
export class LocalStorageService {

  constructor() { }

  /**
   * Store item in local storage
   * @param key 
   * @param value 
   */
  store(key, value): void {
    localStorage.setItem(key, value);
  }

  /**
   * Get item from local storage based on key
   * @param defaultValue 
   */
  get(key, defaultValue?: any): any {
    return localStorage.getItem(key) || defaultValue;
  }

  /**
   * Removes the key-value pair from local storage based on key
   * @param key 
   */
  remove(key): void {
    localStorage.removeItem(key);
  }

  /**
   * Store an object in local storage
   * @param key 
   * @param value 
   */
  storeObject(key, value): void {
    localStorage[key] = JSON.stringify(value);
  }

  /**
   * Get the object from local storage based on given key
   * @param key
   * @param defaultValue 
   */
  getObject(key, defaultValue?: any): any {
    return JSON.parse(localStorage.getItem(key) || defaultValue);
  }
}



