import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {Observable} from 'rxjs/Observable';
import { BlogServerService } from '../../services/blog-server.service';
import { UserAuthService } from '../../services/user-auth.service';
import { AlertService } from '../../services/alert.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  blogArray: any = [];
  categories: any;
  message: string;

  constructor(private alert: AlertService, private blog: BlogServerService, private auth: UserAuthService, private route: Router) { }

  ngOnInit() {
    this.getBlogsByUser(this.auth.username);
  }

  /**
   * Get blog posts by user
   * @param author 
   */
  getBlogsByUser(author) {
    this.blog.getBlogsByUser(author)
    .subscribe(value => {
      if(typeof(value) === 'string'){
        this.message = value;
      }else{
        this.blogArray = value;
      }
    });
  }

  /**
   * Get blog details for a given blog
   * @param blog 
   */
  blogDetail(blog) {
    this.route.navigate(['/blog', blog.category, blog._id]);
  }

  /**
   * Delete blog with given blog id
   * @param blog_id 
   */
  deleteBlog(blog_id) {
    this.blog.deleteBlog(blog_id).subscribe(value => {
      if(value){
        this.getBlogsByUser(this.auth.username);
        this.alert.success("Blog deleted", true);
      }
    });
  }

  /**
   * Go to edit page
   * @param id 
   */
  edit(id){
    this.route.navigateByUrl(`/edit/${id}`);
  }
}


