import { FormControl, Validators, AbstractControl } from '@angular/forms';

export class FormValidators implements Validators{
    constructor() {}

    checkPassword(str: FormControl)
    {
      // at least one number, one lowercase and one uppercase letter
      // at least six characters
      let re = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,}/;
      return re.test(str.value) ? null : { inValidPassword: true };
    }

    checkUsername(str: FormControl) {
        // Username must contain only letters, numbers and underscores
        let re = /^\w+$/;
        return re.test(str.value) ? null : { inValidUsername: true };
    }

    checkEmail(str: FormControl) {
        let re = /^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i;
        return re.test(str.value) ? null : { inValidEmail: true };
    }
}


