import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { CategoriesService } from './services/categories.service';
import { BlogServerService } from './services/blog-server.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  categories: any;

  constructor(private catService: CategoriesService, private route: Router, private blog: BlogServerService){}

  ngOnInit(){
    this.getCategories();
  }

  /**
   * Get the list of categories
   */
  getCategories(): void {
    this.catService.getCategories().then(categories => {
      this.categories = categories;
    });
  }

  /**
   * Navigation to the category page
   * @param name 
   */
  navCategories(name) {
    this.route.navigate(['/blog', name]);
  }

  /**
   * Show sidebar based on route conditions
   */
  show() {
    if (this.route.url === '/auth/login' || this.route.url === '/auth/signup' ||
    this.route.url === '/home' || this.route.url === '/create' || this.route.url.startsWith('/edit')){
      return false;
    }else {
      return true;
    }
  }

  ngDestroy(){
    
  }
}



