import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BlogServerService } from '../../services/blog-server.service';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent implements OnInit {

  constructor(private actroute: ActivatedRoute, private blog: BlogServerService, private route: Router) { }

  category: any;
  data: any;
  message: string;
  public blogs: any;
  private sub: any;
  private blogsub: any;
  private countsub: any;
  detailsub: any;
  skip: number = 0;
  count: number = 0;
  page: number = 1;

  ngOnInit() {
    this.skip = 0;
    this.sub = this.actroute.params.subscribe(params => {
      this.category = params['category'];
      this.getBlogDetails(this.category, this.page);
    });
  }

  /**
   * Function to get blogs by category
   * @param category : Category to which the blog belongs
   * @param skip : Number of blogs to skip
   */
  getBlogDetails(category, skip) {
    this.message = '';
    this.blogs = [];
    skip = skip-1;
    this.blogsub = this.blog.getBlogByCategories(category, skip)
    .subscribe(value => {
      if(typeof(value) === 'string'){
        this.message = value;
      }else{
        this.blogs = value;
      }
    },
    error => {
      this.message = error;
    });
    // update blog count
    this.countsub = this.blog.getBlogCount(this.category).subscribe(value => {
      this.count = value;
    })
  }

  /**
   * Function to retrieve the blog details for a given blog
   * @param blog
   */
  blogDetail(blog) {
    this.route.navigate(['/blog', blog.category, blog._id]);
  }

  getMore(){
    this.skip += 5;
    this.getBlogDetails(this.category, this.skip);
  }

  ngDestroy(){
    this.sub.unsubscribe();
    this.blogsub.unsubscribe();
    this.countsub.unsubscribe();
  }
}



