import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { NavModule } from './components/nav/nav.module';
import { routing } from './app.routing';
import { HomeModule } from './components/home/home.module';
import { UserAuthService } from './services/user-auth.service';
import { LocalStorageService } from './services/local-storage.service';
import { ActivateGuard } from './guards/activate.guard';
import { DeactivateGuard } from './guards/deactivate.guard';
import { BlogDetailModule } from './components/blog-detail/blog-detail.module';
import { CategoriesModule } from './components/categories/categories.module';
import { UserModule } from './components/user/user.module';
import { UserBookmarkModule } from './components/user-bookmark/user-bookmark.module';
import { BlogServerService } from './services/blog-server.service';
import { SidebarModule } from './components/sidebar/sidebar.module';
import { CategoriesService } from './services/categories.service';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { AlertModule } from './components/alert/alert.module';
import { AlertService } from './services/alert.service';
import { EditBlogModule } from './components/edit-blog/edit-blog.module';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    NgbModule.forRoot(),
    BrowserModule,
    NavModule,
    HomeModule,
    routing,
    SidebarModule,
    CategoriesModule,
    UserModule,
    UserBookmarkModule,
    BlogDetailModule,
    AlertModule,
    EditBlogModule
  ],
  providers: [
    CategoriesService,
    BlogServerService,
    UserAuthService,
    LocalStorageService,
    ActivateGuard,
    DeactivateGuard,
    AlertService,
    {provide: LocationStrategy, useClass: HashLocationStrategy}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



