import { Component, OnInit, Input } from '@angular/core';
import { DatePipe, UpperCasePipe  } from '@angular/common';
import { BlogServerService } from '../../services/blog-server.service';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { UserAuthService } from '../../services/user-auth.service';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css']
})
export class CommentsComponent implements OnInit {

  @Input() blog_id: number;
  comments: any = [];
  message: any;
  comment = new FormControl('', [Validators.required]);

  commentForm: FormGroup = this.fb.group({
    comment: this.comment
  })

  constructor(private auth: UserAuthService, private fb: FormBuilder, private blog: BlogServerService) { }

  ngOnInit() {
    this.blog.getBlogComments(this.blog_id)
    .subscribe(value => {
      // sort comments based on date time in increasing order
      this.comments = value.sort((a,b) => {
        return new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime();
      });
    });
  }

  /**
   * This function is used to post a comment to the blog's comment array
   * @param model 
   */
  post(model){
    this.blog.postBlogComments(this.blog_id, model).then((response) => {
      if(response.status === 200){
        this.comments.push({
          comment: model.comment,
          postedBy: {
            username: this.auth.username
          },
          updatedAt: Date.now()
        });
      }
    });
  }

}



