import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FormValidators } from '../../validators/form.validator';
import {Observable} from 'rxjs/Observable';
import { User } from '../../user';
import { UserAuthService } from '../../services/user-auth.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  error: string = '';
  private data: Observable<string>;
  
  constructor(private fb: FormBuilder, private route: Router, private auth: UserAuthService) { }

  ngOnInit() {
  }

  fv = new FormValidators();

  username = new FormControl('', [Validators.required, this.fv.checkUsername]);
  email = new FormControl('', [Validators.required, Validators.email, this.fv.checkEmail]);
  password = new FormControl('', [Validators.required, this.fv.checkPassword]);
  rePassword = new FormControl('', [Validators.required, this.fv.checkPassword]);

  signupForm: FormGroup = this.fb.group({
    username: this.username,
    email: this.email,
    password: this.password,
    rePassword: this.rePassword
  })

  signup(model) {
    this.error = '';
    this.data = new Observable(observer => {
      setTimeout(() => {
          observer.next(this.auth.message);
      }, 1000);
    });

    let subscription = this.data.subscribe(
      value => this.error = value,
      error => this.error = error
    )

    this.auth.register(model);

    if(this.auth.isLoggedIn){
      subscription.unsubscribe();
    }
  }

  gotoLogin() {
    this.route.navigateByUrl('/auth/login');
  }

}



