import { Injectable } from '@angular/core';
import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { CreateBlogComponent } from '../components/create-blog/create-blog.component';

@Injectable()
export class DeactivateGuard implements CanDeactivate<CreateBlogComponent> {

  canDeactivate(component: CreateBlogComponent){
      let can = component.canDeactivate();
      if(!can){
        if(window.confirm('Are you sure you want to continue? Any unsaved changes will be erased.')){
          return true;
        }else{
          return false;
        }
      }
      return true;
  }
}



