import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BlogServerService } from '../../services/blog-server.service';
import { UserAuthService } from '../../services/user-auth.service';

@Component({
  selector: 'app-user-bookmark',
  templateUrl: './user-bookmark.component.html',
  styleUrls: ['./user-bookmark.component.css']
})
export class UserBookmarkComponent implements OnInit {

  constructor(private blog: BlogServerService, private auth: UserAuthService, private route: Router) { }
  
  blogArray: any = [];
  categories: any;
  message: any;

  ngOnInit() {
    this.getUserBookmarks();
  }

  /**
   * Get bookmarks by user
   */
  getUserBookmarks() {
    this.blog.getUserBookmarks()
    .subscribe(value => {
      if(typeof(value) === 'string'){
        this.message = value;
        this.blogArray = [];
      }else{
        this.blogArray = value;
      }
    });
  }

  /**
   * Get blog details based on blog
   * @param blog 
   */
  blogDetail(blog) {
    this.route.navigate(['/blog', blog.category, blog._id]);
  }
}



