import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Headers } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { Router } from '@angular/router';
import { DatePipe } from '@angular/common';
import { BlogServerService } from '../../services/blog-server.service';
import { UserAuthService } from '../../services/user-auth.service';

@Component({
  selector: 'app-blog-detail',
  templateUrl: './blog-detail.component.html',
  styleUrls: ['./blog-detail.component.css']
})
export class BlogDetailComponent implements OnInit {
  id: number;
  likes: any = 0;
  category: any;
  private sub: any;
  detail: any;
  bookmarks: any;
  error: boolean;

  constructor(private router: Router, private auth: UserAuthService, private route: ActivatedRoute, private blog: BlogServerService) { }

  private message: string = '';

  ngOnInit() {
    // get param id from route
    this.sub = this.route.params.subscribe(params => {
      this.id = params['id'];
    });
    // subscribe to get blog detail with given id
    this.blog.getBlogDetail(this.id).subscribe(value => {
      this.detail = value;
      if(typeof(value) === 'string'){
        this.error = true;
      }
      this.likes = value.likes;
      this.bookmarks = value.bookmarks;
    });
  }

  /**
   * Toggle user's bookmarks
   */
  toggleBookmarkBlog () {
    // if user is logged in
    if(this.auth.isLoggedIn){
      this.blog.saveUserBookmarks(this.id).subscribe(value => {
        this.bookmarks = value;
      });
    }else{
      this.router.navigateByUrl('/auth/login');
    }
  }

  /**
   * Toggle likes on a blog by a user
   */
  toggleLike() {
    // if user is logged in
    if(this.auth.isLoggedIn){
      this.blog.postLikes(this.id).subscribe(value => {
        this.likes = value;
      });
    }else{
      this.router.navigateByUrl('/auth/login');
    }
  }

  ngDestroy(){
    this.sub.unsubscribe();
  }
}



