import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DatePipe, UpperCasePipe  } from '@angular/common';
import { CategoriesService } from '../../services/categories.service';
import { BlogServerService } from '../../services/blog-server.service';
import { UserAuthService } from '../../services/user-auth.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  // Array to hold the top 10 blogs
  blogArray: any = [];
  private sub: any;
  token: any;
  user: any;
  uid: any;
  message: any;
  error: any;
  categories: any;
  count: any = [];

  constructor(
    private catService: CategoriesService,
    private blog: BlogServerService,
    private route: Router,
    private actRoute: ActivatedRoute,
    private auth: UserAuthService) { }

  ngOnInit(): void {
    this.findTopBlogs();

    this.getCategories();

    this.sub = this.actRoute.queryParams.subscribe(params => {
      this.token = params['token'];
      this.user = params['user'];
      this.uid = params['_id'];
    });

    if(this.token){
      this.auth.authToken = this.token;
      this.auth.isLoggedIn = true;
      this.auth.headers.append('x-access-token', this.auth.authToken);
    }
    if(this.user){
      this.auth.username = this.user;
    }
    if(this.uid){
      this.auth._id = this.uid;
    }

  }

  /**
   * Get the list of categories
   */
  getCategories(): void {
    this.catService.getCategories().then(categories => {
      this.categories = categories;
      categories.forEach((element,i) => {
        this.blog.getBlogCount(element['name']).subscribe(val => {
          this.count[i] = val;
        });
      });
    });
  }

  /**
   * Navigation to the category page
   * @param name
   */
  navCategories(name) {
    this.route.navigate(['/blog', name]);
  }

  /**
   * Function to retrieve the top 10 blogs
   */
  findTopBlogs(){
    this.blog.findTopBlogs()
    .subscribe((value) => {
      if(typeof(value) === 'string'){
        this.error = true;
        this.message = value;
        return;
      }
      this.blogArray = value;
    });
  }

  /**
   * Navigate to blog page
   * @param blog
   */
  blogDetail(blog) {
    this.route.navigate(['/blog', blog.category, blog._id]);
  }
}



